@import url(https://fonts.googleapis.com/css2?family=Source+Serif+Pro:wght@300&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

body {
  font-family: 'Source Serif Pro', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1250px;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
  margin: 0 auto;
}

a {
  color:burlywood;
}

.App{padding:10px;text-align:left;justify-self:center}.app-home-section{text-align:center;justify-self:center}.win-card{background-color:#507011}.lost-card{background-color:#66250e}

.MatchDetailsCard{display:grid;grid-template-columns:1fr 1fr;padding:20px;margin-top:0px;margin-bottom:5px;max-width:1200px;align-self:center}.MatchDetailsCard .date-section,.MatchDetailsCard .venue-section,.MatchDetailsCard .result-section{margin-top:10px}.MatchDetailsCard .innings-section{text-align:right}.MatchDetailsCard .innings-section>p{margin-bottom:10px}

.MatchSmallCard{margin-bottom:5px;padding:10px;height:150px}.MatchSmallCard .result-section{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:.5fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:7px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;max-width:1200px;justify-self:center}.TeamPage .latest-matches-label{margin-top:0px}.TeamPage .team-name-section{grid-column:span 3;justify-self:stretch;align-self:center;height:100px;margin-bottom:0px}.TeamPage .team-name-section .team-name-label{font-size:4rem;margin-bottom:0px;margin-top:0px}.TeamPage .win-loss-section{padding:35px;text-align:center;height:220px;margin-bottom:0px}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{justify-self:center;font-size:1.5rem;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}

.YearSelector{justify-self:end}.YearSelector .selector-list{list-style-type:none}.YearSelector .selector-list li{margin-top:15px;margin-bottom:15px}

.MatchPage{display:grid;grid-template-columns:1fr 3fr;padding:20px;grid-column-gap:20px;justify-self:center;max-width:1100px}.MatchPage .year-selector{justify-self:end}

.TeamTileCard{height:150px;text-align:center;border:1px solid white;-webkit-align-self:center;align-self:center;padding:20px;background-color:#631d08}

.HomePage{justify-self:center;text-align:center;-webkit-align-self:center;align-self:center;max-width:800px;margin-left:200px}.HomePage .home-header-section{margin:20px}.HomePage .home-teams-section{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px;justify-self:center;text-align:center;align-self:center}

