.HomePage {
    justify-self: center;
    text-align: center;
    align-self: center;
    max-width: 800px;
    margin-left: 200px;
    
    .home-header-section {
        margin: 20px;
    }

    .home-teams-section {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 10px;
        grid-row-gap: 10px;
        justify-self: center;
        text-align: center;
        align-self: center;
        
        
    }
}