.TeamPage {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: .5fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 7px;
    height: fit-content;
    max-width: 1200px;
    justify-self:center;

    .latest-matches-label {
        margin-top: 0px;
    }
    .team-name-section {
        grid-column: span 3;
        justify-self: stretch;
        align-self: center;
        height: 100px;
        margin-bottom: 0px;

        .team-name-label {
            font-size: 4rem;
            margin-bottom: 0px;
            margin-top: 0px;
        }
    }
    .win-loss-section {
        
        padding:35px;
        text-align: center;
        height: 220px;
        margin-bottom: 0px;
    }
    .match-detail-section {
        grid-column: span 4;
    }
    .more-link {
        justify-self: center ;
        font-size: 1.5rem;
        height: fit-content;
    }
}