.MatchDetailsCard {
    display :grid;
    grid-template-columns: 1fr 1fr;
    padding: 20px;
    margin-top: 0px;
    margin-bottom: 5px;
    max-width: 1200px;
    align-self: center;
    
    

    .date-section, .venue-section, .result-section {
        margin-top: 10px;
    }

    .innings-section {
        text-align: right;
        & > p {
            margin-bottom: 10px;
        }
    }

}

